#updraftcentral-search-area {
  flex-direction: column;
}

#udc_search_tag {
  width: 100%;
}

.updraftcentral-search-area input[type="text"] {
	height: 1.7rem;
	line-height: 1rem;
	font-size: 1rem;
	padding: 0rem 0.5rem;
	border-radius: 0.25rem !important;

	&:focus {
		color: rgb(55, 57, 63);
		border-color: rgb(55, 57, 63);
		box-shadow: 0 0 0 2px rgb(55, 57, 63);
	}
}

#updraftcentral-search-info {
	font-style: italic;
	font-size : 1rem;
}
